export default function checkID(idStr: string) {
  // 
  const letters = ['A', 'B', 'C', 'D',
    'E', 'F', 'G', 'H', 'J', 'K', 'L', 'M',
    'N', 'P', 'Q', 'R', 'S', 'T', 'U', 'V',
    'X', 'Y', 'W', 'Z', 'I', 'O'];
  // 
  const multiply = [1, 9, 8, 7, 6, 5, 4, 3, 2, 1];
  const nums = new Array(2);
  let firstNum;
  let total = 0;
  // 
  // 128
  const regExpID = /^[a-z][1-2]\d{8}$/i;
  // 

  if (idStr.search(regExpID) === -1) {
    // 
    return false;
  }
  // 
  const firstChar = idStr.charAt(0).toUpperCase();
  const lastNum = parseInt(idStr.charAt(9), 10);

  // 
  for (let i = 0; i < 26; i += 1) {
    if (firstChar === letters[i]) {
      firstNum = i + 10;
      nums[0] = Math.floor(firstNum / 10);
      nums[1] = firstNum - (nums[0] * 10);
      break;
    }
  }
  // 
  for (let i = 0; i < multiply.length; i += 1) {
    if (i < 2) {
      total += nums[i] * multiply[i];
    } else {
      total += parseInt(idStr.charAt(i - 1), 10) * multiply[i];
    }
  }
  // 
  if (lastNum === 0 && (total % 10) !== lastNum) {
    return false;
  }

  return !(lastNum !== 0 && (10 - (total % 10)) !== lastNum);
}
